/* TransactionForm.css */
.form-container {
    background-color: #f4f4f4;
    padding: 1rem;
    margin-bottom: 1rem;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .form-container select,
  .form-container input[type="number"],
  .form-container button {
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 3px;
  }
  
  .form-container button {
    background-color: #333;
    color: white;
    border: none;
    cursor: pointer;
  }
  