/* TransactionList.css */
.transaction-list {
    background-color: #f4f4f4;
    padding: 1rem;
    margin-bottom: 1rem;
    border-radius: 5px;
  }
  
  .transaction-list h2 {
    background-color: #333;
    color: white;
    text-align: center;
    padding: 0.5rem;
    border-radius: 1px;
    margin: 0;
  }
  
  .transaction-list table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .transaction-list th,
  .transaction-list td {
    padding: 0.5rem;
    border-bottom: 1px solid #ccc;
    text-align: left;
  }
  
  .transaction-list th {
    background-color: #333;
    color: white;
  }
  